.progressbar {
    border: solid 2px;
    border-color: #9D9D9D;
    padding: 2px;
    width: 135px;
    height: 18px;
    margin-left: auto;
    margin-right: auto;

    .progress {
        background-color: #2ECC71;
        height: 100%;
        width: 0%;
        transition: width 200ms;
    }
}
