.story {

    & .story-separator {

        height: 2px;
        width: 100%;
        background-color: rgba(255,255,255,0.3);
    }

    & .story-name {

        text-transform: capitalize;
        font-style: italic;
        font-size: 14px;
        opacity: 0.9;

        display: inline-block;
    }

    & .story-btn {

        margin-left: 10px;

        cursor: pointer;
        font-size: 14px;
        opacity: 0.5;

        &:hover {
            opacity: 0.9;
        }

        display: inline-block;
    }

    & .story-fullscreen {

        height: 16px;
        vertical-align: middle;
    }

    & .story-source {
        white-space: pre-wrap;
        font: 12px/normal 'Monaco', 'Menlo', 'Ubuntu Mono', 'Consolas', 'source-code-pro', monospace;
        background-color: #141414;
        color: #F8F8F8;
        padding: 5px;
    }
}