
.element-drag-button {

    display: flex;
    height: 64px;
    width: 300px;
    position: relative;
    background-color: #0F1012;

    &:hover {
        background-color: #000000;
    }

    &-draggable {

        position: absolute;
        width: 290px;
        height: 54px;
        margin: 6px;
        opacity: 0;

        .element-drag-button:hover & {
            stroke: #ffffff;
            opacity: 0.1;
        }

    }

    &-text {
        &-left {
            color: white;
            font-family: 'Open Sans', sans-serif;
            margin-left: 20px;
            font-size: 16;
            font-weight: 600;
            margin-right: auto;
        }

        &-right {
            opacity: 0.3;
            color: white;
            font-family: Heebo, sans-serif;
            font-weight: 600;
            font-size: 16;
            text-align: right;
            margin-right: 20px;
        }
    }

    &-icon {
        
        opacity: 0.3;
        stroke: #fff;
        fill: #fff;

        .element-drag-button:hover &,
        &:hover {
            opacity: 1;
            stroke: #2ECC71;
            fill: #2ECC71;
        }
    }

    &-square {
        height: 64px;
        color: #ffffff3c;
        transition: color 100ms;
        width: 64px;
    }

    &-rec {
        width: 236px;
        height: 64px;
        position: absolute;
        margin-left: 64px;
        display: flex;
        align-items: center;
        align-content: center;
        flex-direction: row;
        transition: background-color 100ms;
    }
}