$darkGreenTint: #1f8f4d;
$darkGreenTintDarker: #155e33;
.context-menu {
	box-shadow: 3px 3px 5px rgb(24, 24, 24);
	width: 200px;
	background-color: rgb(41, 41, 41);
	padding-top: 5px;
	padding-bottom: 5px;
	z-index: 99999999;
	font-size: 14px;
	font-weight: 360;
	position: absolute;
	&-button {
		
		&-name{
			flex-grow: 1;
		}
		&-icon{
			margin-top: 2px;
			font-size: 12px;
			width: 26px;
			text-align: center;
		}
		display: flex;
		height: 24px;
		padding-left: 10px;
		padding-top: 2px;
		font-size: 13px;
		color:#cccccc;
		font-weight: 400;
		&:hover {
			background-color: $darkGreenTint;
			color:white;
		}
		&:active {
			background-color: $darkGreenTintDarker;
			color:white;
		}
		&-group{
			position: absolute;
			left:200px;
			display: none;
			background-color: rgb(41, 41, 41);
			box-shadow: 3px 3px 5px rgb(24, 24, 24);
			padding-top: 5px;
			padding-bottom: 5px;
			top:0;
			text-overflow: ellipsis;
			overflow: hidden;
			white-space: nowrap;
			padding-left: 1px;
			&-button{
				padding-right: 10px;
			}
		}
	}
	&-separator {
		height: 1px;
		background-color: rgb(63, 61, 61);
		margin-left: 10px;
		margin-right: 10px;
		margin-top: 5px;
		margin-bottom: 5px;
	}
}
