.componentPage {

    & .componentName {
        text-transform: uppercase;
        font-size: 20px;
        font-weight: 800;
        margin-bottom: 30px;
    }

    & .section
    {
        background-color: white;

        @media (prefers-color-scheme: dark) {
            background-color: #3b3e46;
        }
        
        border-radius: 4px;
        margin-bottom: 10px;
        padding: 20px;
        & .title {
            text-transform: uppercase;
            font-size: 15px;
            font-weight: 800;
            letter-spacing: 1px;
            opacity: 0.9;
            margin-bottom: 10px;

            & .preview-btn {
                cursor: pointer;
                display: inline-block;
                margin-right: 20px;
            }

            & .unselected {
                opacity: 0.3;
            }

        }
        
    }

    & .documentation
    {

        margin: 50px 0;

        & .title {
            text-transform: uppercase;
            font-size: 15px;
            font-weight: 800;
            letter-spacing: 1px;
            opacity: 0.9;
        }
        
    }

    & .stories 
    {

        & .title {
            text-transform: uppercase;
            font-size: 15px;
            font-weight: 800;
            letter-spacing: 1px;
            opacity: 0.9;
        }

        & .stories-none {

            font-style: italic;
        }
    }
}