.element-menu {

	&-loading {
	
		background-color: #2ECC71;
		height: 100%;
		left: 0;
		right: 100%;
		position: absolute;

		&.progress {
			right: 0;
			transition: right 300ms;
		}

		&.completed {
			right: 0;
		}
	}

	&-icon{

		stroke: #fff;
		fill: #fff;

		&.idle {
			opacity: 0.3;

			&:hover {
				opacity: 0.5;
			}
		}

		&.active {
			opacity: 0.99999; /* For some reason, opacity: 1 doesn't work */
		}
	}

	
	&-button {
		display: flex;
		height: 64px;
		width: 64px;
		position: relative;

		&-icon {
	
			opacity: 0.3;
			stroke: #fff;
			fill: #fff;
	
			&-active {
				opacity: 1;
				stroke: #2ECC71;
				fill: #2ECC71;
			} 

			&:hover,
			&.selected {
				opacity: 0.5;
			}
		}
	
		&-square {
			background-color: black;
			height: 64px;
			color: #ffffff3c;
			transition: color 100ms;
			width: 64px;
		}
	
		&-rec {
			width: 236px;
			height: 64px;
			position: absolute;
			margin-left: 64px;
			background-color: #000000;
			display: flex;
			align-items: center;
			align-content: center;
			flex-direction: row;
			transition: background-color 100ms;

			&:hover,
			&.selected {
				background-color: #111111;
			}
		}
	
		&-element-sub {
	
			width: 300px;
			/*height: 64px;*/
			position: absolute;
			background-color: #000000;
			display: flex;
			align-content: center;
			flex-direction: column;
			&:hover {
				background-color: #111111;
			}
			transition: background-color 100ms;
	
			&.using-mouse {
				margin-left: 300px;
			}
			
		}
	
		&-sub-menus {

			&.with-offset {
				margin-left: 236px; /* the size of a sub menu*/
			}
		}
	
		&-more-rec {
			width: 364px;
			height: 448px;
			background-color: #000000;
			position: absolute;
			margin-left: 64px;
			display: flex;
		}
	
		&-green-line {
			height: 2px;
			width: 64px;
			background-color: #2ecc71;
		}
	
		&-text {
			&-left {
				color: white;
				font-family: 'Open Sans', sans-serif;
				margin-left: 20px;
				font-size: 16;
				font-weight: 600;
				margin-right: auto;
			}
	
			&-right {
				opacity: 0.3;
				color: white;
				font-family: Heebo, sans-serif;
				font-weight: 600;
				font-size: 16;
				text-align: right;
				margin-right: 20px;
			}
		}
	
		&-arrow {
			opacity: 0.3;
			stroke: white;
			margin-left: -40px;
			margin-right: -20px;
			width: 64px;
			height: 64px;
		}
	
		&-sub {
			&-icon {
				color: #ffffff3c;
				text-align: center;
				font-size: 20px;
				font-family: 'Open Sans', sans-serif;
				height: 64px;
			}
			&-texts {
				background-color: #00000040;
				width: 300px;
				height: 100%;
				flex-direction: column;
			}
			&-icons {
				background-color: #000000;
				width: 64px;
				height: 100%;
			}
			&-columns {
				width: 364px;
				display: flex;
				flex-direction: column;
				height: 100%;
			}
		}
	
		&-main {
			&-sub {
				height: 64px;
				display: flex;
				justify-content: center;
				align-content: center;
				flex-direction: column;
				transition: background-color 100ms;
				&:hover {
					background-color: #111111;
				}
			}
			&-rec {
				width: 300px;
				height: 128px;
				background-color: #000000;
				position: absolute;
				margin-left: 64px;
				display: flex;
			}
			&-square {
				width: 12px;
				height: 12px;
				margin-left: 268px;
				position: absolute;
			}
			&-selected {
				width: 20px;
				height: 20px;
				border: 2px solid #ffffff6c;
				margin-left: 264px;
				position: absolute;
			}
		}
	
		&:hover > .element-menu-button-square > .element-menu-button-icon {
			opacity: 0.5;
		}
	}
}