.prop-field-borderInput {

    display: flex;

    &-modeButton {

        opacity: 0.7;
        width: 40px;
        text-align: center;
        margin: 2px;

        &:hover {
            opacity: 1;
            cursor: pointer;
        }

    }

    &-sides {

        display: flex;
        flex-direction: column;
    }

    &-side {

        display: flex;

        &-label {
            font-size: 12px;
            width: 100px;
        }

    }

}