.prompt {
	width: 100%;
	height: 100%;
	left: 0;
	top: 0;
	position: absolute;
	backdrop-filter: blur(2px);
	background-color: rgba(255, 255, 255, 0.014);
	z-index: 999999;
	&-content {
		position: absolute;
		left: calc(50% - 250px);
		top: calc(50% - 100px);
		background-color: rgb(20, 20, 20);
		width: 500px;
		height: 200px;
		padding: 20px;
		white-space: pre;
	}
	&-buttons {
		position: absolute;
		right: 20px;
		bottom: 20px;
	}
	&-first-button {
		margin-right: 10px;
	}
	input {
		background-color: rgb(43, 43, 43);
		margin-top: 10px;
		width: 300px;
	}
	&-title {
		font-size: 20px;
		margin-bottom: 0px;
	}
}
