.markdown{
    h1{
        font-size: 24px;
        margin-bottom: 20px;
    }
    h2{
        font-size: 18px;
        margin-bottom: 20px;
        margin-top: 40px;
    }

    h3{
        font-size: 16px;
        margin-bottom: 20px;
        margin-top: 40px;
    }

    p{
        margin-bottom: 20px;
        font-size: 15px;
    }

    ul{
        margin-left: 40px;
        margin-bottom: 20px;
        font-size: 16px;
    }

    blockquote{
        white-space: pre;
        border-radius: 4px;
        background-color:rgba(255,255,255,0.1);
        padding:10px 20px;
        
        p{
            margin-bottom: 0;
            font-size: 12px;
            color:rgba(255,255,255,0.6);
        }
    }

    .code-block{
        position: relative;
        .code-language{
            color:rgba(255,255,255,0.6);
            font-size: 12px;
            padding: 3px 7px;
            border-radius: 0 0 0 4px;
            position: absolute;
            right:0;
            background-color: rgba(255,255,255,0.1);
        }
    }
    

    pre{
        background-color:rgba(255,255,255,0.1);
        border-left: 20px solid rgba(255,255,255,0.05);
        padding: 10px 15px;
        border-radius: 3px;
        margin-bottom: 20px;
        font-size: 14px;
        line-height: 24px;
        code{
            font-family: Consolas, monaco, monospace;
            width: 100%;
            display: block;
            overflow-x: auto;
            white-space: pre-wrap;
            word-wrap: break-word;
            
        }
    }

    a{
        color:#13afca;
        &:visited{
            color:#13afca;
        }
    }

    table{
        width: 100%;
        border:1px solid rgba(255,255,255,0.2);
        margin-bottom: 20px;
        color:rgba(255,255,255,0.8);
        border-spacing: 0;
        font-size: 15px;
        thead{
            background-color: rgba(255,255,255,0.1);
            border:none;
            th{
                font-weight: 600;
            }
        }
        th, td{
            border-bottom: 1px solid rgba(255,255,255,0.2);
            border-right: 1px solid rgba(255,255,255,0.2);
            text-align: left;
            padding: 10px 15px;
        }

        th:last-child,td:last-child{
            border-right: none;
        }

        tr:last-child td{
            border-bottom:none
        }
    }
}
