
.dspVisualizer{
    background-color: #E8E8E8;
    color:#333;

    @media (prefers-color-scheme: dark) {
        background-color: #1E2024;
        color:white;
    }


    width: 100vw;
    height: 100vh;
    font-family: 'Open Sans', sans-serif;

    overflow-x: hidden;

    &.padded{
        padding-left: 200px;
    }
    
    & .menu{
        background-color: white;

        @media (prefers-color-scheme: dark) {
            background-color: black;
        }


        width: 200px;
        padding: 20px 20px;
        font-size: 14px;
        position: fixed;
        bottom: 0;
        top: 0;
        left:0;

        & .packageInfo{
            font-weight: 600;
            margin-bottom: 20px;

            & .name{
                font-size: 14px;
                overflow:hidden;
                text-overflow: ellipsis;
                width: 100%;
            }

            & .version{
                font-size: 12px;
                opacity: 0.7;

                /* handle text overflow */
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                max-width: 100vw;
            }
        }

        &-title{

            margin-top: 20px;

            text-transform: uppercase;
            font-size: 12px;
            font-weight: 800;
            letter-spacing: 1px;

            color: rgba(0,0,0,0.7);

            @media (prefers-color-scheme: dark) {
                color:rgba(255,255,255,0.3);
            }
        }

        .menu-sub {

            & .menu-sub-header{

                display: flex;

                cursor: pointer;
                opacity: 0.8;

                &:hover {
                    opacity: 1;
                }

                & .menu-sub-header-title{

                    font-size: 14px;
                    text-transform: capitalize;
                }

                & .menu-sub-header-btn{

                    margin-right: 5px;
                    font-family: monospace;
                    font-size: 16px;
                }
            }

            & .menu-sub-content {
                padding-left: 10px;
            }
        }

        .menu-doc, .menu-component, .menu-tool {
            cursor: pointer;
            opacity: 0.7;

            &:hover {
                opacity: 1;
            }
        }

        .menu-story {

            display: flex;
            justify-content: space-between;

            &-btn {

                cursor: pointer;
                opacity: 0.7;

                &:hover {
                    opacity: 1;
                }
            }

            &-fullscreen-icon {

                height: 16px;
                vertical-align: middle;

            }

        }
    }

    & .content{
        
        width: 100%;
        height: 100%;
        padding: 50px;
        position: relative;
        overflow: auto;

        &.no-margin{
            padding:0;
        }

        .sdk-documentation{
            padding: 50px;
        }
    }
}