.ctrlmenu{

	&-loading {
	
		background-color: #2ECC71;
		height: 100%;
		left: 0;
		right: 100%;
		position: absolute;

		&.progress {
			right: 0;
			transition: right 300ms;
		}

		&.completed {
			right: 0;
		}

	}

	&-icon{

		stroke: #fff;
		fill: #fff;

		&.idle {
			opacity: 0.3;

			&:hover {
				opacity: 0.5;
			}
		}

		&.active {
			opacity: 0.99999; /* For some reason, opacity: 1 doesn't work */
		}
	}
}


