.spa-root {

    height: 100%;
    background-color: #fff;

    > .spa-modal{
        
        position: absolute;
        z-index: 1;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        overflow: auto;
        background-color: rgb(0,0,0); /* Fallback color */
        background-color: rgba(0,0,0,0.4);

        /* Centering */
        display: flex;
        justify-content: center;
        align-items: center;

        .action-process-content {

            

        }
    }
}

html.dark {

    .spa-root {
        background-color: #1e2024;
    }
}