$navigation-width: 200px;

.story{
    width: 100%;
    height: 100%;
}

.test-canvas{
    background-color: #1E2024;
    color:white;
    width: 100vw;
    height: 100vh;
    font-family: 'Open Sans', sans-serif;

    overflow-x: hidden;
    padding-left: 200px;

    &-nav{
        background-color: black;
        width: $navigation-width;
        padding: 20px 20px;
        font-size: 14px;
        position: fixed;
        bottom: 0;
        top: 0;
        left:0;

        &-brand{
            font-weight: 800;
            margin-bottom: 20px;

            &-name{
                font-size: 16px;
            }
            &-project{
                font-size: 12px;
                opacity: 0.7;

                /* handle text overflow */
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                max-width: 100vw;
            }
        }

        &-menu {

            &-title{
                text-transform: uppercase;
                font-size: 12px;
                font-weight: 800;
                letter-spacing: 1px;
                margin-top: 10px;
                opacity: 0.3;
            }

            a{
                display: block;
                color:white;
                text-decoration: none;
                align-items:center;
                padding:5px 0;

                svg{
                    fill:#13afca;
                    width:19px;
                    height:19px;
                }
            }
        }
    }

    &-content{
        
        width: 100%;
        height: 100%;
        position: relative;

        &-padded{
            padding: 50px;
        }


    }

    &-doc{
        width: 100%;
        height: 100%;
        padding: 50px;
    }
}

@media screen and (max-width: 576px) {
    .test-canvas-content-padded {
        padding: 10px;
    }
}

@media screen and (max-width: 576px) {
    .test-canvas {
        padding-left: 0;
    }
  }

  @media screen and (max-width: 576px) {
    .test-canvas-nav {
        display: none;
    }
  }