.no-preview {
    opacity: 0.7;
    font-style: italic;
}

.component-playground {
    height: 400px;
    margin-left: -20px;
    margin-right: -20px;
}

.component-preview {

    &.component-preview-content {
        background-color: #fff;
    }

    & .props-fields
    {
        padding-bottom: 20px;
        margin-bottom: 20px;
        border-bottom: 2px solid rgba(255,255,255,0.3);

        & .prop-field {
            max-width: 600px;
            display: grid;
            grid-template-columns: auto auto auto 1fr;
            margin: 8px 0;
            padding-left: 10px;
            background-color: rgba(0,0,0,0.1);
            @media (prefers-color-scheme: dark) {
                background-color: rgba(0,0,0,0.5);
            }
            min-height: 32px;
            align-items: center;
            text-align: left;
            border-radius: 4px;

            &-name {
                font-size: 13px;
                width:100px;
            }

            &-type {
                font-size: 14px;
                font-style: italic;
                margin-left: 8px;
                margin-right: 8px;
            }

            &-description {

                width: 16px;
                height: 16px;

                &-icon {
                
                    width: 100%;

                    &.disabled {
                        opacity: 0.3;
                    }
    
                }
    
                &-text {

                    visibility: hidden;
                    font-size: 12px;
                    width: 120px;
                    background-color: black;
                    color: #fff;
                    text-align: center;
                    border-radius: 6px;
                    padding: 5px 0;

                    /* Position the tooltip */
                    position: absolute;
                    z-index: 1;

                    /* Fade animation */
                    opacity: 0;
                    transition: opacity 500ms;
                }

                &:hover .prop-field-description-text {
                    visibility:visible;
                    opacity: 1;
                }
            }

            input{
                background: white;
                color:#333;

                @media (prefers-color-scheme: dark) {
                    background: black;
                    color:white;
                }
                
                width: 100%;
                height: 29px;

                border:none;        
                border-radius:0 4px 4px 0;
            }
        }
    }

    & .logs-container {

        margin-top: 20px;
        border-top: 2px solid rgba(255,255,255,0.3);
        padding-top: 10px;

        font-style: italic;
        font-size: 14px;

        & .logs {
            max-height: 300px;
            overflow: auto;
    
            & .log {
                font-style: italic;
                font-size: 12px;
            }
        }
    }
}

html.dark {

    & .component-preview-content {
        background-color: #1e2024;
    }

}