.spa-builder{
    
    position: relative;
    height: 100%;
    display: flex;

    > .spa-builder-panel{
        flex:1;
        position: relative;

        transition: flex 200ms;

        &.minimized {
            flex:0;
            width:0;
        }
    }

    &-buttons {

        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 10px;
        width: 26px;
        justify-content: center;
        position: relative;
        background-color: #1E1E1E;
        user-select: none;
        

        &::before{
            content: '';
            position: absolute;
            width: 1px;
            background-color: #626366;
            top:0;
            bottom: 0;
        }

        &-block {
            z-index: 10;
            width: 26px;
            padding: 3px;
            border:1px solid #626366;
            background-color: #1E1E1E;
            display: flex;
            flex-direction: column;
            align-items: center;
            border-radius: 6px;
            gap: 3px;
        }

        &-button {
    
            
            

            &.__tiny{
                height: 18px;
                &.selected{
                    svg path{
                        fill: #2CC66C;
                    }
                    border-radius: 2px;
                    background-color:white;
                }
            }

            &.__text{
                writing-mode: vertical-rl;
                text-orientation: mixed;
                width: 18px;
                background: #626366;
                border-radius: 2px;
                text-transform: uppercase;
                font-family: 'Consolas';
                font-size: 12px;
                color: white;
                opacity: 1;
                padding: 7px 2px;

                &.__auto{
                    background-color:#3498DB;
                }

                &.__manual{
                    background-color:#E74C3C;
                }

                &:hover{
                    background-color:#2CC66C;
                }
            }
        }
    
    }

    &-xml{
        
    }

    &-preview{


        overflow-y: auto;
        height: 100%;

        &-content{
            background-color: #fff;
            overflow: auto;
            height: 100%;

            &-html {
                height: 100%;
                width: 100%;
            }

        }

        &-info{
            
            margin-left: -12px;

            border-top: 1px solid #626366;
            position: absolute;

            z-index: 9999;
            
            left:0;
            right: 0;
            bottom: 0;

            &-bar{
                background-color: #1E1E1E;
                border-top:1px solid #626366;
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;

                &-buttons{
                    display: flex;
                    gap: 3px;
                    margin-left: 10px;
                    padding-bottom: 3px;
                    

                    button{
                        text-transform: uppercase;
                        font-weight: 600;
                        font-size: 14px;
                        font-family: 'Open Sans';
                        padding: 2px 5px;
                        border:1px solid #626366;
                        color: rgba(255,255,255,0.5);
                        cursor: pointer;

                        border-top: none;
                        background: none;
                    }
                }
                
                &-route {
                    display: flex;

                    &-path {
                        color: white;
                        font-size: 14px;
                        font-style: italic;
                        padding-right: 20px;
                    }

                    &-home {
                        color: white;
                        font-size: 14px;

                        &:hover {
                            color: #2CC66C;
                        }
                        padding-right: 20px;
                    }

                }

                
            }

            &-content{
                > div{
                    display: none;
                    width: 100%;
                    background-color: #1E1E1E;
                    color:rgba(255,255,255,0.8);
                    font-family: 'Consolas';
                    padding: 10px;
                    user-select: text !important;
                }
            }

            &.data{
                ._data-btn{
                    background-color: #2CC66C;
                    color: white;
                }

                ._data-view{
                    display: block;
                    white-space:pre;
                    max-height: 500px;
                    overflow: auto;
                    
                }
            }

            &.variables{
                ._variables-btn{
                    background-color: #2CC66C;
                    color: white;
                }

                ._variables-view{
                    display: block;
                    white-space:pre;
                    max-height: 500px;
                    overflow: auto;
                }
            }

            &.store {
                ._store-btn{
                    background-color: #2CC66C;
                    color: white;
                }

                ._store-view{
                    display: block;
                    padding: 6px;
                    background-color: #0d0e0e;

                    ._store-view-data{
                        display: block;
                        white-space:pre;
                        max-height: 500px;
                        overflow: auto;
                    }

                    ._store-view-clear{

                        cursor: pointer;
                        color: #ffffff;
                        opacity: 0.5;
                        width: fit-content;

                        &:hover {
                            opacity: 1;
                        }
                    }
                }
            }

            &.import{
                ._import-btn{
                    background-color: #2CC66C;
                    color: white;
                }
                ._import-view{
                    display: block;
                    white-space:pre;
                }
            }

            &.user{
                ._user-btn{
                    background-color: #2CC66C;
                    color: white;
                }

                ._user-view{
                    display: block;
                    white-space:pre;
                    max-height: 500px;
                    overflow: auto;

                    &-field {
                        display: flex;
                        padding: 10px;

                        &-label {
                            padding-right: 10px;
                        }

                    }
                }
            }

            &.size{
                ._size-btn{
                    background-color: #2CC66C;
                    color: white;
                    opacity: 0.8;
                    padding: 3px;

                    &:hover{
                        opacity: 1;
                        cursor: pointer;
                    }
                }

                ._size-view{
                    display: block;
                    white-space:pre;
                    max-height: 500px;
                    overflow: auto;

                    &-field {
                        display: flex;
                        padding: 10px;

                        &-label {
                            padding-right: 10px;
                        }

                    }

                    &-group {
                        display: flex;
                        padding: 10px;
                        gap: 5px;
                    }

                    &-btn{
                        background-color: #2CC66C;
                        color: white;
                        opacity: 0.8;
                        padding: 3px;
    
                        &:hover{
                            opacity: 1;
                            cursor: pointer;
                        }
                    }
                }
            }

            &.console{
                ._console-btn{
                    background-color: #2CC66C;
                    color: white;
                }
                ._console-view{
                    display: block;
                    padding: 6px;
                    background-color: #0d0e0e;
                    
                    ._console-view-lines{
                        max-height: 400px;
                        overflow-y: auto;

                        ._console-view-line{
                            margin-bottom: 5px;
                            color: #ffffff;
                        }
                    }

                    ._console-view-clear{

                        cursor: pointer;
                        color: #ffffff;
                        opacity: 0.5;
                        width: fit-content;

                        &:hover {
                            opacity: 1;
                        }

                    }
                }
            }
        }
    }
}

html.dark {

    .spa-builder-preview-content {
        background-color: #1e2024;
    }
}