.workflowclient {

    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;

    &-wrapper {
        /*
        min-width: 320px;
        max-width: 320px;
        */
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &-content {
            position: relative;
            /*
            height: 100px;
            */

            &-description {
                padding: 10px;
                /*
                position: absolute;
                */
                max-height: 80px;
                font-size: 16px;
                color: #9D9D9D;
                width: 100%;
                font-family: 'Open Sans', sans-serif;

                &.loading {
                    text-align: center;
                }
            }

            &-title {
                text-align: center;
                text-transform: uppercase;
                font-size: 20px;
                color: #9D9D9D;
                font-family: 'Open Sans', sans-serif;
            }

            &-icon {
                text-align: center;
            }

            &-dots {
                text-align: center;
                overflow: visible;

                &-dot {
                    padding: 10px;
                    display: inline-block;
                    animation-name: bounce;
                    animation-duration: 1200ms;
                    animation-iteration-count: infinite;
                    animation-timing-function: linear;

                    &.dot-1 {
                        animation-delay: 0ms;
                    }

                    &.dot-2 {
                        animation-delay: 200ms;
                    }

                    &.dot-3 {
                        animation-delay: 400ms;
                    }
                }
            }

            @keyframes bounce {
                0% {
                    transform: translate(0, 0);
                }

                25% {
                    transform: translate(0, -25px);
                    animation-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
                }

                50% {
                    transform: translate(0, 0);
                }

                100% {
                    transform: translate(0, 0);
                }
            }

            &-form {
                background-color: white;
                color: black;
                text-align: center;

                & .no-form {
                    font-family: 'Open Sans', sans-serif;
                    font-size: 18px;
                    color: black;
                    text-align: center;
                }

                & .completeButton {
                    background-color: deepskyblue;
                    opacity: 1;
                    color: white;
                    cursor: pointer;
                    width: 100px;
                    margin: auto;
                    text-align: center;
                    border-radius: 10px;

                    &:hover {
                        opacity: 0.8;
                    }
                }
            }
        }

        &-powered {
            font-size: 12px;
            color: #9D9D9D;
            font-family: 'Open Sans', sans-serif;
            text-align: center;

            &-brand {
                color: #13AFCA;
            }
        }
    }
}
