.inspector-root {
    display: flex;
    flex-direction: column;
    width: 64px;
    align-items: flex-end;
}

.inspector-container {

    background-color: black;
    width: 300px;

    font-family: 'Open Sans', sans-serif;
}

.inspector-content {
    display: flex;
    flex-direction: column;
    padding: 10px;
}

.inspector-menu-button {

    background-color: black;
    height: 64px;
    color: #ffffff3c;
    transition: color 100ms;
    width: 64px;

    &-active {
        background-color: #2ECC71;
    }

    &-icon {

        opacity: 0.3;
		stroke: #fff;
		fill: #fff;

		&.active {
			opacity: 1;
			stroke: #fff;
			fill: #fff;
		} 

		&.error {
			opacity: 1;
			stroke: #E74C3C;
			fill: #E74C3C;
		} 

    }
}

.inspector-header {

    font-weight: bold;
    color: #fff;
    opacity: 0.2;
    padding-bottom: 15px;
    padding-top: 20px;
}

.inspector-green-line {
    
    height: 2px;
    width: 100%;

    background-color: #2ecc71;
}