.prop-field-thicknessInput {

    display: flex;

    &-sideSelection {

        display: flex;

        &-button {

            opacity: 0.7;
            width: 16px;
            text-align: center;
            margin: 2px;

            &.selected {
                opacity: 1;
            }

            &:hover {
                opacity: 1;
                cursor: pointer;
            }

        }

    }

}